






























import Vue from 'vue';
import movieRepository from '@/services/repositories/MovieAPI/movieRepository/MovieRepository';
import getImage from '@/utilities/getImage';
import { MovieDetailsState } from './types';

export default Vue.extend({
  name: 'MovieDetails',
  data() {
    return {
      fetching: false,
      movie: {},
      movieTrailer: {},
      similarMovies: {},
    } as MovieDetailsState;
  },
  components: {
    MoviesItem: () => import('@/components/MoviesItem.vue'),
    MovieDetailsGenres: () => import('@/views/movieDetails/MovieDetailsGenres.vue'),
    MovieDetailsLinks: () => import('@/views/movieDetails/MovieDetailsLinks.vue'),
    MovieDetailsRating: () => import('@/views/movieDetails/MovieDetailsRaiting.vue'),
  },
  props: {
    movieId: {
      type: String,
      required: true,
    },
  },
  computed: {
    isMediumDevice(): boolean {
      return this.$vuetify.breakpoint.mdAndUp;
    },
    flexClass(): string {
      const styleClass = this.isMediumDevice ? 'd-flex' : 'd-flex flex-column';
      return styleClass;
    },
    image(): string {
      const { poster_path: poster, backdrop_path: backdrop } = this.movie;
      if (poster !== undefined && backdrop !== undefined) {
        return this.isMediumDevice ? getImage('original', poster) : getImage('original', backdrop);
      }
      return '';
    },
    imageWidth(): string {
      return this.isMediumDevice ? '350px' : '100%';
    },
  },
  methods: {
    async movieDetails(): Promise<void> {
      const { data } = await movieRepository.details(this.movieId);
      this.movie = data;
    },
    async getMovieTrailer(): Promise<void> {
      const {
        data: { results },
      } = await movieRepository.trailer(this.movieId);

      const trailer = results.find((movie) => movie.type === 'Trailer' && movie.site === 'YouTube');
      this.movieTrailer = trailer || {};
    },
    async getSimilarMovies(): Promise<void> {
      const {
        data: { results },
      } = await movieRepository.similar(this.movieId);
      this.similarMovies = results;
    },
    initComponent(): void {
      this.fetching = true;
      this.movieDetails();
      this.getMovieTrailer();
      this.getSimilarMovies();
      this.fetching = false;
    },
  },
  watch: {
    movieId() {
      this.initComponent();
    },
    movie() {
      document.title = `Movie Maniac | ${this.movie.title}`;
    },
  },
  created() {
    this.initComponent();
  },
});
